import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ServiceProviderService } from '../core/service-provider.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-woofcare',
  templateUrl: './woofcare.page.html',
  styleUrls: ['./woofcare.page.scss'],
})
export class WoofcarePage implements OnInit {
  services: any;
  constructor(
    private utility: UtilityService,
    private providerService: ServiceProviderService,
    private activeRoute: ActivatedRoute
  ) {}

  ngOnInit() {
    this.getServiceCategoriesList();
  }

  getServices() {
    this.providerService.getServiceCategory().subscribe((res) => {
      this.services = res;
    });
  }

   getServiceCategoriesList(){

      this.providerService.serviceCatList().subscribe(
      (res) => {
        console.log(res);
        this.services = res;
        this.utility.dismiss();
      },
      (err) => {
        console.log(err);
        this.utility.dismiss();
        this.utility.showAlert('Something went wrong!');
      }
    );
    }
}
